export class LoggerOperationResult {
    Result: boolean;
    Message?: string;
    Code?: number;
    Content?: any;

    constructor(Result: boolean, Message: string) {
        this.Result = Result;
    }
}

export interface TBServerInfos {
    tread: TBServerInfo[];
}

export class TBServerInfo {
    DateTime = new Date();
    ProcessName = '';
    ProcessId = '';
    LoginNumber?: number;
    DocumentNumber?: number;

    DocMetrics?: any[];
    LernelMS?: number;
    LoginInfos?: any[];
    UserMS?: number;
    VirtualMemory = 100000;
    PhisicalMemory = 100000;

    threads?: any[];
}
